<template>
	<scroll 
  :type="1" :loop="loop" :data="carouselData" :click="true"
  @scroll-end="scrollEnd" @beforeScrollStart="beforeScrollStart"
  ref="slider"
  class="carousel">
		<div 
    class="carousel-group" 
    :style="{ width: pageWidth*pageLength + 'px' }">
			<a 
			class="carousel-item"
			:style="{ width: 100 / pageLength + '%' }"
			v-for="data of carouselData"
			:href="data.linkUrl">
				<img 
        @load.once="loadend"
				class="image"
				:src="data.picUrl">
			</a>
		</div>
		<div class="dot-group">
			<div 
			class="dot" 
			:class="{active: pageIndex === index}"
			v-for="(dot, index) of dotLength"></div>
		</div>
	</scroll>
</template>

<script>
import scroll from './base/scroll.vue'
import betterScroll from 'better-scroll';

const RECOMMENTPAGE = 0;

export default {

  name: 'carousel',
  props: {
    carouselData: {
      type: Array,
      default: null
    },
    slider: {
      type: Object,
      default: null
    },
    interval: {
      type: Number,
      default: 4000
    },
    autoPlay: {
	    type: Boolean,
	    default: true
    },
    loop: {
      type: Boolean,
      default: true
    },
    currentPage: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      dotLength: 0,
      pageWidth: 0,
      pageLength: 0,
      pageIndex: 0,
      timer: null,
      carousel: null
    };
  },
  watch: {
    carouselData: function(){
      this.initCarousel();
    },
    currentPage: function(newVal){
      if(newVal === RECOMMENTPAGE) {
        this.play();
      } else {
        this.stop();
      }
    }
  },
  methods: {
    initCarousel: function() {
    	/* init length */
    	this.dotLength = this.carouselData.length;
    	this.pageLength = this.loop ? 
    					  this.carouselData.length + 2 :
    					  this.carouselData.length;

    	/* init page width */
    	this.pageWidth = this.$el.clientWidth;

    	this.$nextTick(() => {
        this.$refs.slider.refresh();
    	});

    	/* play */
    	this.play();
    },
    beforeScrollStart: function() {
      this.slider.disable();
      if (this.autoPlay) {
        clearTimeout(this.timer);
      }
    },
    scrollEnd: function() {
      this.slider.enable();
      if(this.loop) {
        this.pageIndex = this.$refs.slider.getCurrentPage().pageX - 1;
      } else {
        this.pageIndex = this.$refs.slider.getCurrentPage().pageX;
      }

      if(this.autoPlay) {
        this.play();
      }
    },
    play: function() {
      let pageIndex = this.loop ? this.pageIndex + 2 : this.pageIndex + 1;
      clearTimeout(this.timer);
	    this.timer = setTimeout(() => {
        this.$refs.slider.goToPage(pageIndex, 0, 400)
      }, this.interval)
    },
    stop: function(){
      clearTimeout(this.timer);
    },
    loadend: function(){
      this.$emit('loadend');
    }
  },
  components: {
    scroll
  }
};
</script>

<style lang="scss" scoped>
.carousel {
  width: 100%;
  position: relative;

  @at-root
  .carousel-group {
  	line-height: 0;
	width: 700%;
	
	@at-root
	.carousel-item {
	  display: inline-block;
	  line-height: 0;
	  width: calc(100% / 7);
	  position: relative;

	  @at-root
	  .image {
	  	width: 100%;
	  }
	}

  	@at-root
  	.dot-group {
  		left: 50%;
  		bottom: 5%;
  		transform: translate(-50%);
  		position: absolute;

  		@at-root
  		.dot {
  			border-radius: 50%;
  			background: rgba(255, 255, 255, .5);
  			display: inline-block;
  			margin: 0 3px;
  			height: 8px;
  			width: 8px;
  			z-index: 1;
  		}

  		@at-root
  		.active {
  			border-radius: 4px;
  			background: rgba(255, 255, 255, .8);
  			width: 16px;
  		}
  	}
  }
  
}
</style>


