<template>
  <transition name="slide">
  <div class="list" :class="{'album-list': albumMsg}">
    <img 
    :src="imgUrl" 
    class="coverPic" 
    :style="{
      filter: 'blur('+blur+'px)', 
      top: title === '荧光大大' ? '-80px' : '-40px'
    }">
    <div class="coverBG"></div>
  	<div class="header">
  	  <i class="exitBtn icon" @click="exit">&#xe605;</i><!-- 
   --><scrollText :data="title" class="scroll-wrap">
        <div class="title">{{title}}</div>
      </scrollText>
  	</div>
    <div 
    :style="{top: menuTop}"
    class="menu-box" 
    v-show="data.type === 'singer'">
      <div 
      class="menu" 
      @click="pageIndex = index"
      :class="{activeMenu: pageIndex === index}"
      v-for="(menu, index) of menus">{{menu}}</div>
    </div>
    <scroll 
    ref="slider"
    :type="1"
    :data="data"
    :bounce="false"
    @scroll-end="scrollEnd"
    class="slider">
      <div 
      :class="{'singerContent-box': data.type === 'singer'}"
      class="content-box" >
        <scroll 
        ref="songScroll"
        :data="list.songList" :probeType="3" 
        class="scroll" 
        @scroll="scroll" @pullingUp="getList">
            <div class="song-box" >
              <div 
              class="song" 
              v-for="(song,index) of list.songList">
                <img :src="rank[index]" 
                class="rank" 
                v-if="index < 3 && data.type==='rank'"><!-- 
             --><div class="rank" v-else-if="index >= 3 && data.type==='rank'">{{index+1}}</div><!-- 
             --><div class="songMsg" @click="play(song)">
                  <div class="songName">{{song.songname}}</div>
                  <div class="other">{{getOtherMsg(song)}}</div>
                </div><!-- 
             --><div class="btn-box">
                  <transition name="scale">
                       <i 
                       class="add-Btn icon" 
                       @click="add(song,index)"
                       v-show="!existIndexs.includes(index)">&#xe652;</i>
                  </transition>
                  <transition name="scale">
                       <i 
                       class="add-Btn icon" 
                       v-show="existIndexs.includes(index)">&#xe648;</i>
                  </transition>
                </div>
              </div>
              <loading v-show="!loadend"></loading>
            </div>
        </scroll><!-- 
     --><scroll 
        ref="albumScroll"
        :data="albums"
        :probeType="3" 
        @scroll="scroll"
        class="scroll album-scroll" 
        v-show="data.type === 'singer'">
          <div class="album-box">
             <loading v-show="!albums.length"></loading>
             <div 
             @touchend="toAlbumList(index)"
             class="song" 
             v-for="(album,index) of albums">
               <img 
               :src="album.img" 
               class="albumPic"><!-- 
            --><div class="songMsg">
                 <div class="songName">{{album.albumName}}</div>
                 <div class="other">{{album.pubTime}}</div>
               </div>
             </div>
          </div>
        </scroll>
      </div>
    </scroll>
    <keep-alive>
      <router-view 
      @update:playerPage="updatePlayerPage"
      :playerPage="playerPage" 
      :albumMsg="albums[albumIndex]"
      style="height:calc(100% + 60px)"></router-view>
    </keep-alive>
  </div>
  </transition>
</template>

<script>
import scroll from './base/scroll.vue'
import scrollText from './base/scroll-text.vue'
import loading from './base/loading.vue'

import jsonp from 'jsonp'
import axios from 'axios'
import co from 'co'
import listBus from '../api/list-bus.js'
import bus from '../api/bus.js'
import router from '../router/index.js'
import {Base64} from 'js-base64'

import rank0 from '../assets/rank0.png'
import rank1 from '../assets/rank1.png'
import rank2 from '../assets/rank2.png'

let entry = location.hash;

export default {

  name: 'list',
  props: {
    playerPage: {
      type: Number,
      default: 0
    },
    singermid: {
      type: String,
      default: ''
    },
    albumMsg: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      data: {},
      albums: [],   // 存储专辑或电台列表
      list: {
        songList: []
      },
      imgUrl: '',
      title: '',
      blur: 0,
      total: 0,
      pagenum: 0,
      loadend: false,
      menus: ['热门', '专辑'],
      pageIndex: 0,
      albumIndex: -1,
      menuTop: 0,
      existIndexs: [],
      playSongList: []
    };
  },
  beforeRouteEnter (to, from, next) {
    next((vm)=> {
      /* 初始化数据 */
      vm.total = 0;
      vm.pagenum = 0;
      vm.pageIndex = 0;
      vm.albumIndex = -1;
      vm.loadend = false;
      vm.list = {
        songList: []
      };
      vm.albums = [];
      vm.menuTop = 0;
      vm.blur = 0;
      vm.rank = [rank0, rank1, rank2];
      vm.existIndexs = [];

      /* 判断是否是专辑列表页面， 如果是则通过参数方式传数据 */
      /* 不是专辑页面，则通过bus获取数据 */
      if(vm.albumMsg) {
        vm.getAlbumList();
      } else {
        vm.data = listBus.get();
        vm.getList();
      }
      
      /* 初始化slider */
      vm.$refs.slider.goToPage(0, 0, 0);
      vm.$refs.slider.refresh();

      /* 请求播放列表数据 */
      bus.$emit('requestProps');
    });
  },
  beforeRouteLeave (to, from, next) {
    next();
  },
  created() {
    bus.$on('props', ([key, value])=> {
      if(key === 'songList') {
        this.playSongList = value;
      }
    });
  },
  methods: {
    /* 获取歌单数据类函数 */
    getList() {
      if(this.loadend === true) {
        return ;
      }

      if(this.data.type === 'rank'){
        this.getRankList();
      } else if(this.data.type === 'singer') {
        this.getSingerList();
      } else if(this.data.type === 'diss') {
        this.getDiscList();
      } else if(this.data.type === 'user') {
        this.imgUrl = this.data.img;
        this.title = this.data.name;
        this.getRadioList();
      }
    },
  	getRankList() {
  	  const URL = `https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg?g_tk=5381&uin=0&format=json&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&tpl=3&page=detail&type=top&topid=${this.data.id}&_=1503320629451`;

  	  jsonp(URL, {param: 'jsonpCallback'}, (err, data)=> {
  	  	if(!err) {
          let songList = [];
          for(let song of data.songlist) {
            song.data.platform = 'QQ';
            songList.push(song.data);
          }

  	  	  this.$set(this.list, 'songList', songList);
          this.imgUrl = data.topinfo.pic_v12;
          this.title = data.topinfo.ListName;
          this.loadend = true;

          this.findExistSong();
  	  	}
  	  });

      router.replace(`?${Base64.encode(`rank&${this.data.id}`)}`);
  	},
    getSingerList() {
      if(this.data.platform === 'WangYi' && !this.loadend) {
        co(this.getWangYiSingerList());
      } else if(!this.loadend){
        this.getQQSingerList();
      }
    },
    getQQSingerList() {
      let mid = this.data.mid ? this.data.mid : this.data.singermid;

      const URL = `https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg?g_tk=5381&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0&singermid=${mid}&order=listen&begin=${this.pagenum*30}&num=30&songstatus=1`;
      
      jsonp(URL, {param: 'jsonpCallback'}, (err, data)=> {
        if(!err) {
          for(let song of data.data.list) {
            song.musicData.platform = 'QQ';
            this.list.songList.push(song.musicData);
          }

          this.imgUrl = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${data.data.singer_mid}.jpg?max_age=2592000`;
          this.title = data.data.singer_name;
          this.total = data.data.total;

          if((this.pagenum + 1)*30 <= this.total) {
            this.pagenum++;
            this.loadend = false;
          } else {
            this.loadend = true;
          }

          this.findExistSong();
        }
      });

      router.replace(`?${Base64.encode(`singer&${mid}`)}`);
    },
    *getWangYiSingerList() {
      const URL = `http://localhost:8080/artists?id=${this.data.id}`;
      
      const singerData = yield axios.get(URL);
      for(let song of singerData.data.hotSongs) {
        let music = {
          id: song.id,
          songid: song.id,
          album: {
            id: song.al.id,
            name: song.al.name
          },
          name: song.name,
          songname: song.name,
          albumname: song.al.name,
          artists: song.ar,
          platform: 'WangYi'
        }
        this.list.songList.push(music);
      }

      this.imgUrl = singerData.data.artist.picUrl;
      this.title = singerData.data.artist.name;
      this.total = singerData.data.artist.musicSize;
      this.loadend = true;

      this.findExistSong();

      router.replace(`?${Base64.encode(`singer&${this.data.id}`)}`);
    },
    getDiscList() {
      const URL = `https://c.y.qq.com/qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg?type=1&json=1&utf8=1&onlysong=0&disstid=${this.data.dissid}&format=jsonp&g_tk=5381&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0`;

      jsonp(URL, {param: 'jsonpCallback', name: 'jsonpCallback'}, (err, data)=> {
        if(!err) {
          for(let song of data.cdlist[0].songlist) {
            song.platform = 'QQ';
          }

          this.$set(this.list, 'songList', data.cdlist[0].songlist);
          this.imgUrl = data.cdlist[0].logo;
          this.title = data.cdlist[0].dissname;
          this.loadend = true;

          this.findExistSong();
        } else {
          console.log(err)
        }
      });

      router.replace(`?${Base64.encode(`disc&${this.data.dissid}`)}`);
    },
    getAlbum() {
      if(this.data.platform === 'WangYi') {
        co(this.getWangYiAlbum());
      } else {
        this.getQQAlbum();
      }
    },
    getQQAlbum() {
      let mid = this.data.mid ? this.data.mid : this.data.singermid;

      const URL = `https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_album.fcg?g_tk=146122906&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0&singermid=${mid}&order=time&begin=0&num=30&exstatus=1`;

      jsonp(URL, {param: 'jsonpCallback', name: 'jsonpCallback'}, (err, data)=> {
        if(!err && data.code === 0) {
          if(data.data.list) {
            this.albums = data.data.list;
            for(let [index,album] of this.albums.entries()) {
              this.$set(this.albums[index], 'img', `https://y.gtimg.cn/music/photo_new/T002R68x68M000${album.albumMID}.jpg?max_age=2592000`);
            }
          }
        }
      });
    },
    *getWangYiAlbum() {
      const URL = `http://localhost:8080/artist/album?id=${this.data.id}`;

      const data = yield axios.get(URL)

      for(let [index, album] of data.data.hotAlbums.entries()) {
        let publishTime = new Date(album.publishTime);
        let al = {
          id: album.id,
          albumName: album.name.trim(),
          pubTime: `${publishTime.getFullYear()}-${publishTime.getMonth()}-${publishTime.getDate()}`,
          img: album.picUrl,
          platform: 'WangYi'
        };

        this.$set(this.albums, index, al);
      }
    },
    getAlbumList() {
      if(this.albumMsg.platform === 'WangYi') {
        this.getWangYiAlbumList();
      } else {
        this.getQQAlbumList();
      }
    },
    getQQAlbumList() {
      const URL = `https://c.y.qq.com/v8/fcg-bin/fcg_v8_album_info_cp.fcg?albummid=${this.albumMsg.albumMID}&g_tk=280129858&&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0`;

      this.jsonpCallback(URL).then((data)=> {
        for(let song of data.data.list) {
          song.platform = 'QQ';
        }
        
        this.$set(this.list, 'songList', data.data.list);
        this.imgUrl = `https://y.gtimg.cn/music/photo_new/T002R300x300M000${data.data.mid}.jpg?max_age=2592000`;
        this.title = data.data.name;
        this.loadend = true;

        this.findExistSong();
      }).catch((err)=> {
        console.error(err);
      });

      router.replace(`?${Base64.encode(`album&${this.albumMsg.mid}`)}`);
    },
    getWangYiAlbumList() {
      const URL = `http://localhost:8080/album?id=${this.albumMsg.id}`;

      axios.get(URL).then((data)=> {
        let songs = [];
        for(let song of data.data.songs) {
          let music = {
            id: song.id,
            songid: song.id,
            album: {
              id: song.al.id,
              name: song.al.name
            },
            name: song.name,
            songname: song.name,
            albumname: song.al.name,
            artists: song.ar,
            singer: song.ar,
            platform: 'WangYi'
          }

          songs.push(music);
        }

        this.$set(this.list, 'songList', songs);
        this.imgUrl = data.data.album.picUrl;
        this.title = data.data.album.name;
        this.loadend = true;

        this.findExistSong();
      });

      router.replace(`?${Base64.encode(`album&${this.albumMsg.id}`)}`);
    },
    getRadioList() {
      const URL = `http://localhost:8080/user/dj?uid=${this.data.id}`;

      axios.get(URL).then((data)=> {
        console.log(data);
        for(let radio of data.data.programs) {
          let music = {
            id: radio.mainTrackId,
            songid: radio.mainTrackId,
            album: {
              id: this.data.id,
              name: radio.mainSong.album.name
            },
            description: radio.description,
            name: radio.name,
            songname: radio.name,
            albumname: radio.mainSong.album.name,
            artists: radio.mainSong.artists,
            img: radio.coverUrl,
            platform: 'WangYi'
          }

          this.list.songList.push(music);
        }
        console.log(this.list.songList)

        this.loadend = true;
        this.findExistSong();
      });

      router.replace(`?${Base64.encode(`user&${this.data.id}`)}`);
    },
    getOtherMsg(song) {
      if(this.data.type === 'singer') {
        if(song.albumname !== '空'){
          return `${song.albumname}`;
        } else {
          return '';
        }
      } else if(this.data.type === 'user'){
        return song.description;
      } else {
        let singers = '';
        song.singer.forEach((singer, index)=> {
          if(index > 0) {
            singers += '&';
          }

          singers += singer.name;
        });

        return `${singers}·${song.albumname}`;
      }
    },
    findExistSong() {
      let existArray = [];

      for(let song of this.playSongList) {
        for(let [index, music] of this.list.songList.entries()) {
          if(Number(song.songid) === Number(music.songid)) {
            if(!this.existIndexs.includes(index)) {
              this.existIndexs.push(index);
            }
            existArray.push(index);
            break;
          }
        }
      }

      this.existIndexs.deleteSomeExcept(existArray);
    },
    /* 辅助类函数 */
    jsonpCallback(url) {
      return new Promise((resolve, reject)=> {
        jsonp(url, {param: 'jsonpCallback', name: 'jsonpCallback'}, (err, data)=> {
          if(err) {
            reject(err);
          } else {
            resolve(data);
          }
        });
      });
    },
    /* 滚动触发函数 */
    scroll(pos) {
      this.scrollMenu(pos);
      this.getBlur(pos);
    },
    scrollEnd() {
      this.pageIndex = this.$refs.slider.getCurrentPage().pageX;

      if(this.pageIndex === 1 && this.albums.length === 0) {
        this.getAlbum();
      }
    },
    scrollMenu(pos) {
      let marginTop = document.body.clientWidth*0.6 - 40;

      if(Math.abs(pos.y) >= marginTop) {
        this.menuTop = `-${marginTop}px`;
      } else {
        this.menuTop = `${pos.y}px`;
      }

      if(pos.target === this.$refs.songScroll.$el) {
        this.$refs.albumScroll.scrollTo(0, parseInt(this.menuTop), 0);
      } else {
        this.$refs.songScroll.scrollTo(0, parseInt(this.menuTop), 0);
      }
    },
  	getBlur(pos) {
      let rate = -pos.y/(document.body.clientWidth*0.6);

      this.blur =  rate > 1 ? 1 : 0;
  	},
    /* 操作类函数 */
  	play(song) {
      bus.playList(this.list.songList, song);  
  	},
    add(song, index) {
      bus.addSong(song);
    },
    toAlbumList(index) {
      console.log(index)
      this.albumIndex = index;
      router.push(`${location.pathname}/list`);
    },
  	exit() {
  	  if(entry === '#/' || entry === '') {
  	  	this.show = false;
  	  	window.history.back();
  	  } else {
  	  	location.hash = '#/';
  	  	entry = '#/';
  	  }
  	},
    updatePlayerPage(playerPage) {
      this.$emit('update:playerPage', playerPage);
    }
  },
  watch: {
    pageIndex(newVal) {
      this.$refs.slider.goToPage(newVal, 0, 400);
    },
    playSongList() {
      this.findExistSong();
    }
  },
  components: {
  	scroll,
    scrollText,
    loading
  }
};
</script>

<style lang="scss" scoped>
.list {
  background: #222;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 60px);
  z-index: 3;

  @at-root
  .coverPic {
    position: absolute;
    top: -30px;
  	width: 100%;
    min-height: calc(60vw + 80px);
  }

  @at-root
  .coverBG {
    width: 100%;
    padding-top: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(black, .2);
  }

  @at-root
  .header {
  	position: absolute;
  	top: 0;
  	left: 0;
	  width: 100%;
	  margin-top: .5rem;
	  z-index:1;

  	@at-root
  	.exitBtn {
  	  color: #ffcd32;
  	  margin: 0 .5rem;
	    position: absolute;
	    top: 0;
	    left: 0;
  	}
    @at-root
    .scroll-wrap {
      width: 70%;
      margin: 0 auto;

      @at-root
      .title {
        color: #ffcd32;
        text-align: center;
      }
    }
  }

  @at-root
  .menu-box {
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 60%;
    background: #333;
    height: 40px;
    width: 100%;
    display: flex;
    z-index: 1;

    @at-root
    .menu {
      color: rgba(white, .9);
      display:inline-block;
      flex: 1;
      font-size: .85rem;
      margin: 0 3rem;
      text-align: center;
      line-height: 40px;
    }

    @at-root
    .activeMenu {
      color: #ffcd32;
      border-bottom: 1.5px #ffcd32 solid;
    }
  }

  @at-root
  .slider {
    width: 100%;
    overflow: hidden;
    position: absolute;
    top: 2rem;
    bottom: 0;
    padding-top: 60%;

    @at-root
    .content-box {
      width: 100%;
      height: 100%;

      @at-root
      .scroll {
        display:inline-block;
        width: 100vw;
        height: 100%;
        vertical-align: top;
        
        @at-root
        .song-box {
          vertical-align: top;
          min-height: 100%;
          width: 100%;
          background: #222;

          @at-root
          .song {
            height: 60px;
            border-bottom: 1px rgba(gray, .3) solid;

            @at-root
            .rank {
              color: #ffcd32;
              display: inline-block;
              width: 30px;
              height: 30px;
              margin: 15px .5rem;
              text-align: center;
              line-height: 30px;
              font-weight: bold;
            }
            
            @at-root
            .albumPic {
              color: #ffcd32;
              display: inline-block;
              width: 40px;
              height: 40px;
              margin: 10px 1rem;
              text-align: center;
              line-height: 40px;
              font-weight: bold;
            }

            @at-root
            .songMsg {
              display: inline-block;
              margin: 15px 0 15px .5rem ;
              position: relative;
              vertical-align: top;
              width: calc(100% - 40px - 4rem);

              @at-root
              .songName {
                color: white;
                font-size: .7rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                max-width: 100%;
              }

              @at-root
              .other {
                color: rgba(white, .6);
                font-size: .6rem;
                margin-top: 5px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                max-width: 100%;
              }
            }
            
            @at-root
            .btn-box {
              position: relative;
              float: right;
              padding: 10px;
              height: 40px;
              width: 40px;

              @at-root
              .add-Btn {
                position: absolute;
                top: 0;
                left: 20px;
                color: rgba(white, .85);
                line-height: 60px;
              }
            }
            
          }
        }

        @at-root
        .album-box {
          background: #222;
          display:inline-block;
          width: 100%;
          min-height: 100%;
        }
      }
    }

    @at-root
    .singerContent-box {
      width: 200%;
    }
  }
  
  
}

.album-list {
  height: 100% !important;  /* 防止被路由中的style修改 */
}

.slide-enter-active, .slide-leave-active {
  transition: transform .3s;
  transform: translate3d(0,0,0);
}
.slide-enter, .slide-leave-to {
  transform: translate3d(100%,0,0);
}

.scale-enter-active, .scale-leave-active {
  transition: transform .3s;
  transform: translate3d(0,0,0) scale(1.1);
}
.scale-enter, .scale-leave-to {
  transform: scale(.1);
}
</style>


