import Vue from 'Vue';
import {Song} from '../class/song.js';

let bus = new Vue({});

bus.playSong = function(song) {
  new Song(song).then((music)=> {
  	this.$emit('play', music);
  });
  
}

bus.addSong = function(song) {
  new Song(song).then((music)=> {
  	this.$emit('add', music);
  });
}

bus.audition = function(song) {
  Song.audition(song).then((music)=> {
    this.$emit('audition', music);
  });
}

bus.playList = function(list, currentSong) {
  let songList = [];
  
  for(let song of list) {
  	songList.push(new Song(song));
  }

  Promise.all(songList).then((list)=> {
    new Song(currentSong).then((song)=> {
      this.$emit('playList', [list, song]);
    })
    
  });
  
}

export default bus;


