Array.prototype.delete = function(value) {
  let index = this.indexOf(value);

  if(index !== -1) {
  	this.splice(index, 1);
  }
}

Array.prototype.deleteSomeExcept = function(array) {
  if(array.length === 0) {
    this.splice(0, this.length);
    return ;
  }

  for(let value of this) {
  	let flag = true;
  	let val;

  	for(val of array) {
  	  if(value === val) {
  	  	flag = false;
  	  	break;
  	  }
  	}

  	if(flag) {
  	  this.delete(value);
  	}
  }
}


