<template>
  <div class="search-results">
    <div 
    class="result-singer" 
    v-for="singer of searchResults.singer"
    @click="toSingerList(singer)">
      <img 
      v-lazy="singer.img" 
      class="singerimg"><!-- 
   --><div class="singermsg">
        <div class="singername">{{singer.name}}</div>
        <div class="singerdata" v-show="singer.songnum">单曲:{{singer.songnum}} 专辑:{{singer.albumnum}}</div>
      </div>
    </div>
    <div 
    class="result-singer"
    v-if="searchResults.user"
    @click="toUserList(searchResults.user)">
      <img 
      v-lazy="searchResults.user.img" 
      class="singerimg"><!-- 
  --><div class="singermsg">
        <div class="singername">{{searchResults.user.name}}</div>
        <div class="singerdata">{{searchResults.user.signature}}</div>
      </div>
    </div>
    <div 
    class="result-song" 
    v-for="(song, index) of searchResults.song.list">
      <i class="songicon icon" @click="play(song)">&#xe680;</i><!-- 
   --><div class="songmsg" @click="play(song)">
        <div class="songname">{{formatText(song.songname)}}</div>
        <div class="singername">{{formatText(song.singer[0].name)}}</div>
      </div>
      <i 
      class="add-Btn icon" 
      @click="add(song)"
      v-show="!existIndexs.includes(index)">&#xe652;</i>
      <transition name="scale">
        <i 
        class="add-Btn icon" 
        v-show="existIndexs.includes(index)">&#xe648;</i>
      </transition>
    </div>
    <loading v-show="!loadend"></loading>
  </div>
</template>

<script>
import bus from '../../api/bus.js';
import listBus from '../../api/list-bus.js';
import router from '../../router/index.js';
import decode from '../../api/decode.js';

import loading from '../base/loading.vue'

export default {

  name: 'search-result',
  props: {
  	searchResults: {
  		type: Object,
  		default: null
  	},
    loadend: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      existIndexs: [],
      playSongList: []
    };
  },
  mounted() {
    bus.$on('props', ([key, value])=> {
      if(key === 'songList') {
        this.playSongList = value;
      }
    });

    bus.$emit('requestProps');
  },
  methods: {
  	play(song) {
  	  bus.playSong(song);
  	},
    add(song) {
      bus.addSong(song);
    },
    findExistSong() {
      if(this.searchResults.song.list) {
        let existArray = [];

        for(let song of this.playSongList) {
          for(let [index, music] of this.searchResults.song.list.entries()) {
            if(Number(song.songid) === Number(music.songid)) {
              if(!this.existIndexs.includes(index)) {
                this.existIndexs.push(index);
              }
              existArray.push(index);
              break;
            }
          }
        }

        this.existIndexs.deleteSomeExcept(existArray);
      }
    },
    formatText(text) {
      /* 正常显示韩文 */
      return decode(text);
    },
    toSingerList(singer) {
      listBus.set(singer, 'singer');
      router.push(`${location.pathname}/list`);
    },
    toUserList(user) {
      listBus.set(user, 'user');
      router.push(`${location.pathname}/list`);
    }
  },
  watch: {
    playSongList() {
      this.findExistSong();
    },
    searchResults: {
      handler: function() { 
        if(this.searchResults.song.list){
          this.findExistSong();
        } else {
          this.existIndexs = [];
        }
      },
      deep: true
    }
  },
  components: {
    loading
  }
};
</script>

<style lang="scss" scoped>
.search-results {

  @at-root
  .result-song {
    height: 55px;
    width: 100%;

    .songicon {
      display: inline-block;
      color: rgba(white, .4);
      font-size: 22px;
      text-align: center;
      line-height: 40px;
      width: 40px;
      height: 40px;
      margin: 7.5px;
    }

    .songmsg {
      display: inline-block;
      width: calc(100% - 115px);
      vertical-align: top;

      .songname { 
        color: rgba(white, .6);
        font-size: .8rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        margin: .35rem 0;
      }

      .singername {
        color: rgba(white, .4);
        font-size: .65rem;
      }
    }

    @at-root
    .add-Btn {
      float: right;
      color: rgba(white, .85);
      line-height: 55px;
      padding:0 .5rem;
      margin:0 .5rem;
    }
  }

  @at-root
  .result-singer {
  	height: 55px;
    width: 100%;

    .singerimg {
      border-radius: 50%;
      color:rgba(white, .4);
      display: inline-block;
      width: 40px;
      height: 40px;
      margin: 7.5px;
    }

    .singermsg {
	    display: inline-block;
	    width: calc(90% - 55px);
      vertical-align: top;

      .singername {
      	color: rgba(white, .6);
      	font-size: .8rem;
      	margin: .35rem 0;
      }

      .singerdata {
		    color: rgba(white, .4);
        font-size: .65rem;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
}

.scale-enter-active, .scale-leave-active {
  transition: transform .3s;
  transform: translate3d(0,0,0) scale(1.2);
}
.scale-enter, .scale-leave-to {
  transform: scale(1);
}
</style>


