<template>
  <div class="miniPlayer">
  	<audio class="audio" 
  	:src="currentSong.songUrl"
  	@timeupdate="getCurrentTime"
  	@pause="isPlaying=false"
    @play="isPlaying=true"
  	@playing="isPlaying=true"
    @loadstart="isPlaying=false"
  	@ended="playEnd"
  	@loadedmetadata="sendProps"
    @error="loadFail"
  	ref="audio">
  	</audio>
  <span @click="toPlayer">
	  <img 
	  v-lazy="currentSong.albumImg_small" :class="{rotate: isPlaying}"
	  class="songImg" 
	  v-show="currentSong.albumImg_small">
	  <i 
	  class="songIcon icon" 
	  v-show="!currentSong.albumImg_small">&#xe603;</i>
    <div class="songMsg-box">
	    <scrollText class="songName" 
      :data="currentSong.songName" :align="'left'" :behavior="'scroll'">
		    {{currentSong.songName ? currentSong.songName : 'Windlie Music'}}
	    </scrollText>
	    <div class="singer">
	  	  {{currentSong.singer ? currentSong.singer : '听想听的音乐'}}
	    </div>
	  </div>
  </span>
	<div class="button-box">
	  <svg class="circleProgress" width="100%" height="100%" version="1.1" xmlns="http://www.w3.org/2000/svg">
	    <circle class="circle" cx="22" cy="20.5" r="17.5" stroke="#ffcd32" stroke-width="2.5" fill="transparent" :stroke-dashoffset="progressLength" stroke-dasharray="113">
	    </circle>
	  </svg>
	  <i class="button icon" @click="togglePlay">{{isPlaying ? '&#xe601;' : '&#xe61a;'}}</i><!-- 
   --><span @click="toSongList" class="button icon">&#xe69a;</span>
	</div>
  </div>
</template>

<script>
/**
/* 该组件为控制音乐总组件
/* 其他组件通过bus触发事件操控
/* bus.$emit('ctrl', ['该组件函数名', 参数...])
/* 获取该组件数据：
/* bus.$emit('requestProps')
**/
import scrollText from '../base/scroll-text.vue'

import axios from 'axios';
import {Base64} from 'js-base64';
import bus from '../../api/bus.js';
import Cookies from '../../api/cookies.js';
import router from '../../router/index.js';

const ALLLOOP    = 0,
	  SINGLELOOP = 1,
	  RANDOM     = 2;
const URL = 'http://112.74.38.137:8080/getLyric';
const params = {
  jsonpCallback:'MusicJsonCallback_lrc',
  pcachetime:new Date().getTime(),
  g_tk:5381,
  loginUin:0,
  hostUin:0,
  format:'jsonp',
  inCharset:'utf8',
  outCharset:'utf-8',
  notice:0,
  platform:'yqq',
  needNewCode:0
}
export default {

  name: 'mini-player',

  data () {
    return {
      songList: [],
      currentSong: {
      	songid: 0,
      	songmid: '',
      	albumImg_small: '',
      	albumImg_big: '',
      	songUrl: '',
      	songName: '',
      	singer: '',
      	albumname: '',
      	duration: 0,
      	currentTime: 0,
        lyric: ''
      },
      currentIndex: -1,
      isPlaying: false,
      playMode: ALLLOOP,
      waitPlay: false
    };
  },
  created() {
    // 将本地缓存取出添加到歌单列表
    if(localStorage.songList) {
      this.songList = JSON.parse(localStorage.songList);
    }

  	bus.$on('play', (song)=> {
      let index = this.add(song);

      if(!this.songList[index].songUrl) {
        this.waitPlay = true; 
        this.changeSong(index);
      } else {
        this.changeSong(index);
        this.play();
      }
  	});

    bus.$on('add', (song)=> {
      this.add(song);
    });

    bus.$on('playList', ([songList, currentSong])=> {
      this.songList.splice(0, this.songList.length, ...songList);
      this.currentIndex = -1;
      let index = this.add(currentSong);


      if(!this.songList[index].songUrl) {
        this.waitPlay = true; 
        this.changeSong(index);
      } else {
        this.changeSong(index);
        this.play();
      }
    });

    bus.$on('audition', (song)=> {
      let index = this.add(song);

      this.changeSong(index);
    });

  	bus.$on('ctrl', (ctrl)=> {
  	  if(typeof(ctrl) === 'string'){
        this[ctrl]();
      } else {
      	let [func, ...params] = ctrl;
      	this[func](params);
      }
  	});

  	bus.$on('requestProps', ()=>{
  	  this.sendProps();
  	});
    
  },
  methods: {
    /* 操作类函数 */
  	play(index) {
      if(typeof index !== 'undefined') {
        let [i] = index;
        if(!this.songList[i].songUrl) {
          this.waitPlay = true; 
          this.changeSong(i);
          return ;
        } else {
          this.changeSong(i);
        }
      }
      
  	  if(this.currentIndex !== -1) {
        setTimeout(()=> {
          this.$refs.audio.play().catch((err)=> {});
          bus.$emit('changeProgress', this.$refs.audio.currentTime);
        }, 20);
        this.waitPlay = false;
  	  }
  	},
    add(song) {
      let index = this.isSongExist(song.songid);

      if(index !== -1) {
        return index;
      } else {
        this.songList.push(song);
        return this.songList.length - 1;
      }
    },
  	pause() {
      if(this.$refs.audio){
  	    this.$refs.audio.pause();
      }
  	},
  	togglePlay() {
  	  if(this.isPlaying) {
  	  	this.pause();
  	  } else {
  	  	this.play();
  	  }
  	},
  	playEnd() {
  	  switch(this.playMode) {
  	  	case ALLLOOP: {
  	  	  this.next();
  	  	  break;
  	  	}
  	  	case SINGLELOOP: {
          this.$refs.audio.currentTime = 0;
  	  	  this.play();
  	  	  break;
  	  	}
  	  	case RANDOM: {
  	  	  let random, index;
  	  	  while((random = Math.random()) === 0);
  	  	  index = Math.ceil(random*this.songList.length) - 1;
  	  	  if(this.currentIndex === index) {
  	  	  	this.$refs.audio.currentTime = 0;
  	  	  	this.$refs.audio.play().catch((err)=> {});
  	  	  } else {
  	  	  	this.currentIndex = index;
  	  	  }
          this.play();

  	  	  break;
  	  	}
  	  }
  	},
  	deleteSong([index]) {
  	  this.songList.splice(index, 1);

  	  /* change current index */
  	  if(index < this.currentIndex) {
  	  	this.currentIndex--;
  	  } else if(index === this.currentIndex) {
  	  	  /* if list is empty */
  	  	  if(this.songList.length === 0) {
  	  	  	this.clear();
  	  	  	return ;
  	  	  }
  	  	  /* if delete last one */
  	  	  else if(this.currentIndex === this.songList.length) {
  	  	  	this.currentIndex = 0;
  	  	  }

  	  	this.currentSong = this.songList[this.currentIndex];
        this.play();
  	  }
  	},
    changeSong(index) {
      this.currentSong = this.songList[index];
      this.currentIndex = index;
    },
  	next() {
  	  if(this.songList.length === 1) {
  	  	this.$refs.audio.currentTime = 0;
  	  	this.play();
  	  	return ;
  	  }
  	  this.currentIndex = this.currentIndex + 1 >= this.songList.length ?
  	  					  0 : this.currentIndex + 1;
      this.play();
  	},
  	pre() {
  	  if(this.songList.length === 1) {
  	  	this.$refs.audio.currentTime = 0;
  	  	this.play();
  	  	return ;
  	  }
      this.currentIndex = this.currentIndex - 1 < 0 ?
  	  					  this.songList.length - 1 : this.currentIndex - 1;
      this.play();
  	},
  	changePlayMode() {
      this.playMode = this.playMode < RANDOM ? this.playMode + 1 : ALLLOOP;
      bus.$emit('props', ['playMode', this.playMode]);
  	},
  	changeProgress([progress]) {
      this.$refs.audio.currentTime = this.currentSong.duration*progress;
      bus.$emit('changeProgress', this.$refs.audio.currentTime);
  	},
  	clear() {
  	  this.pause();

	    this.songList.splice(0, this.songList.length);
	    this.currentSong = {
	  	  songid: 0,
	  	  songmid: '',
		    albumImg_small: '',
		    albumImg_big: '',
		    songUrl: '',
		    songName: '',
		    singer: '',
		    albumname: '',
		    duration: 0,
		    currentTime: 0
	    };
	    this.currentIndex = -1;
	    this.isPlaying = false;
	    this.sendProps();
  	},
    /* 获取数据类函数 */
  	getCurrentTime() {
      if(this.currentSong) {
        this.currentSong.currentTime = this.$refs.audio.currentTime;
        bus.$emit('props', ['currentTime', this.currentSong.currentTime]);
      }
  	},
    getLyric() {
      let newParams = Object.assign({}, params, {songmid: this.currentSong.songmid});
      if(this.currentSong.platform === 'WangYi') {
        return axios.get(`http://112.74.38.137:8080/lyric?id=${this.currentSong.songid}`)
               .then((data)=> {
                 if(data.data.lrc) {
                   this.currentSong.lyric = data.data.lrc.lyric;
                 } else {
                   this.currentSong.lyric = '此歌暂无歌词。';
                 }
               });
      } else {
        return axios.get(URL, {params: newParams}).then((data)=> {

          let formatDataStr = data.data.replace('MusicJsonCallback_lrc','')
                             .replace('(','').replace(')','');
          let dataObj = JSON.parse(formatDataStr);

          dataObj.lyric = Base64.decode(dataObj.lyric);

          if(dataObj.lyric !== '�w^~)�t') {
            this.currentSong.lyric = dataObj.lyric;
          } else {
            this.currentSong.lyric = '此歌暂无歌词。';
          }
        }).catch(()=> {
          this.currentSong.lyric = '此歌暂无歌词。';
        });
      }
      
    },
    getWangyiUrl() {
      axios.get(`http://112.74.38.137:8080/music/url?id=${this.currentSong.songid}`)
      .then((data)=> {
        if(Number(this.currentSong.songid) === Number(data.data.data[0].id)) {
          this.$set(this.currentSong, 'songUrl', data.data.data[0].url);
          if(this.waitPlay) {
            this.play();
          }
        }         
      });
    },
  	sendProps() {
	  	if(this.currentIndex !== -1){
	  	  this.currentSong.duration = this.$refs.audio.duration;
	  	}
      bus.$emit('props', ['songList', this.songList]);
      bus.$emit('props', ['currentIndex', this.currentIndex]);
      bus.$emit('props', ['currentSong', this.currentSong]);
      bus.$emit('props', ['isPlaying', this.isPlaying]);
      bus.$emit('props', ['playMode', this.playMode]);
      bus.$emit('props', ['currentTime', this.currentSong.currentTime]);
  	},
    /* 辅助类函数 */
    /**
    /* isSongExist
    /* @param:songid:歌曲序号
    /* 若歌曲存在返回歌曲索引
    /* 不存在返回-1
    **/
    isSongExist(songid) {
      let index = -1;
      for(let [i, music] of this.songList.entries()) {
        if(Number(music.songid) === Number(songid)) {
          index = i;
          break;
        }
      }

      return index;
    },
    loadFail() {
      this.getWangyiUrl();
    },
    /* 路由函数 */
    toSongList() {
      router.push(`${location.pathname}/songList`);
    },
    toPlayer() {
      if(this.currentIndex !== -1){
        router.push(`${location.pathname}/player`);
      }
    }
  },
  watch: {
  	currentIndex(newVal) {
      if(this.currentIndex !== -1 &&
         this.currentSong !== this.songList[this.currentIndex]) {
        this.currentSong = this.songList[this.currentIndex];
      }
  	},
    currentSong(newVal) {
      let index = this.songList.indexOf(this.currentSong);;

      if(this.currentIndex !== index) { 
        this.currentIndex = index;
      }
      this.currentSong.lyric = '此歌暂无歌词。';

      if(this.currentIndex === -1) {
        document.title = 'Windlie Music';
      } else {
        // 如果没有播放链接则获取后再播放
        if(!this.currentSong.songUrl) {
          this.getWangyiUrl();
        }

        if(!this.currentSong.albumImg_small) {
          axios.get(`http://112.74.38.137:8080/album?id=${this.currentSong.albummid}`)
          .then((data)=> {
            if(Number(data.data.code) === 200 &&
              Number(data.data.album.id) === Number(this.currentSong.albummid)) {
              this.$set(this.currentSong, 'albumImg_small', `${data.data.album.picUrl}?param=68y68`);
              this.$set(this.currentSong, 'albumImg_big', `${data.data.album.picUrl}?param=300y300`);
            } else if(Number(data.data.code) === 404) {
              // 获取专辑封面失败后尝试获取电台封面
              axios.get(`http://112.74.38.137:8080/user/dj?uid=${this.currentSong.albummid}`)
              .then((data)=> {
                for(let radio of data.data.programs) {
                  if(Number(this.currentSong.songid) === Number(radio.mainTrackId)) {
                    this.$set(this.currentSong, 'albumImg_small', `${radio.coverUrl}?param=68y68`);
                    this.$set(this.currentSong, 'albumImg_big', `${radio.coverUrl}?param=300y300`);
                    break;
                  }
                }
              });
            }
          });
        }

        this.getLyric().then(()=>{
          bus.$emit('changeProgress', this.currentSong.currentTime);
        }).catch((err)=> {
          bus.$emit('changeProgress', this.currentSong.currentTime);
          console.log(err)
        });

        document.title = `${this.currentSong.songName} - ${this.currentSong.singer}`;
      }

      
      bus.$emit('changeProgress', 0);
      bus.$emit('props', ['currentSong', this.currentSong]);
      bus.$emit('props', ['currentIndex', this.currentIndex]);
    },
  	isPlaying(newVal) {
      bus.$emit('props', ['isPlaying', newVal]);
      bus.$emit('changeProgress', this.$refs.audio.currentTime);
      bus.$emit('props', ['currentTime', this.$refs.audio.currentTime]);
  	},
    songList(newVal) {
      localStorage.songList = JSON.stringify(newVal);
    }
  },
  computed: {
  	progressLength() {
      const CIRCLELENGTH = 113;

  	  return CIRCLELENGTH - this.currentSong.currentTime/this.currentSong.duration*CIRCLELENGTH;
  	},
  },
  components: {
  	scrollText
  }
};
</script>

<style lang="scss" scoped>
.miniPlayer {
  background: #333;
  width:100%;
  height: 60px;
  position: absolute;
  bottom: 0;
  left: 0;

  @at-root
  .audio {
  	display: none;
  }

  @at-root
  .songImg,
  .songIcon {
  	display: inline-block;
  	color: rgba(white, .5);
  	font-size: 40px;
  	width: 40px;
  	height: 40px;
  	border-radius: 50%;
  	animation: rotate 30s linear infinite;
  	-webkit-animation-play-state:paused; /* Safari 和 Chrome */
  	animation-play-state:paused;
  }

  @at-root
  .rotate {
  	-webkit-animation-play-state:running; /* Safari 和 Chrome */
  	animation-play-state:running;
  }

  @at-root
  .songImg {
  	margin: 10px;
  }

  @at-root
  .songIcon {
  	margin: 5px 10px;
  }

  @at-root
  .songMsg-box {
  	display: inline-block;
	  vertical-align: top;
	  padding-top: 10px;
	  width:calc(100% - 172px);
	  overflow: hidden;

  	@at-root
  	.songName {
  	  width: 100%;
  	  height: 1rem;
  	  color: rgba(white,.8);
  	  font-size: .8em;
  	  margin-bottom: 5px;
  	}

  	@at-root
  	.singer {
  	  overflow: hidden;
  	  white-space: nowrap;
  	  text-overflow: ellipsis;
	    color: rgba(white, .5);
	    font-size: .65em;
  	}
  }

  @at-root
  .button-box {
  	position:absolute;
  	right: 0;
  	top:9px;

	@at-root
	.circleProgress {
	  pointer-events:none;
	  position:absolute;
	  left:-4px;
	  top: -1px;
	  width: 42px;
	  height: 42px;

	   @at-root
	   .circle {
	   	transform: rotate(-90deg);
	   	transform-origin: 50% 50%;
	   }
	}

  	@at-root 
  	.button {
  	  display: inline-block;
  	  color:#ffcd32;
  	  margin-right: 10px;
  	  font-size: 36px;
  	}
  }
}

@keyframes rotate {
	from { transform:rotate(0deg); }
	to { transform:rotate(360deg); }
}
</style>


