<template>
  <div>
  	<slot></slot>
  </div>
</template>

<script>
import BetterScroll from 'better-scroll';
export default {

  name: 'scroll',
  
  props: {
    /**
    /*0:竖向
    /*1:横向
    **/
    type: {
      type: Number,
      default: 0
    },
  	probeType: {
  		type: Number,
  		default: 1
  	},
    scrollY: {
      type: Boolean,
      default: true
    },
    scrollX: {
      type: Boolean,
      default: true
    },
  	click: {
  		type: Boolean,
  		default: true
  	},
    bounce: {
      type: Boolean,
      default: true
    },
    snap: {
      type: Boolean,
      default: false
    },
    loop: {
      type: Boolean,
      default: false
    },
    pullUpLoad: {
      type: Boolean,
      default: false
    },
  	data: {
  		type: [Array, Object, String],
  		default: null
  	}
  },
  data () {
    return {
      scroll: null,
      eventList: []
    };
  },
  methods: {
    initScroll: function(){
      if(this.type === 0) {
        this.scroll = new BetterScroll(this.$el, {
          probeType: this.probeType,
          click: this.click,
          snap: this.snap,
          scrollX: this.scrollX,
          scrollY: this.scrollY,
          bounce: this.bounce,
          pullUpLoad: this.pullUpLoad,
          threshold: 0
        });  
      } else {
        this.scroll = new BetterScroll(this.$el, {
          probeType: this.probeType,
          scrollX: true,
          scrollY: false,
          momentum: false,
          snap: true,
          snapLoop: this.loop,
          snapThreshold: 0.3,
          snapSpeed: 400,
          click: this.click,
          bounce: this.bounce,
        });
      }
      
      this.initScrollEvent();
    },
    initScrollEvent: function() {
      this.scroll.on('scroll', (pos)=> {
        pos.target = this.$el;
        this.$emit('scroll', pos);
      });

      this.scroll.on('beforeScrollStart', (pos)=> {
        this.$emit('beforeScrollStart', pos);
      });

      this.scroll.on('scrollEnd', ()=> {
        // 如果竖屏状态下滚到底， 触发上拉事件
        if(this.type === 0 && this.scroll.y <= this.scroll.maxScrollY + 50){
          this.$emit('pullingUp');
          this.scroll.refresh();
        } else {
          this.$emit('scroll-end')
        }
      });
    },
    goToPage: function(xIndex = 0, yIndex = 0, time = 400) {
      if(this.scroll) {
        this.scroll.goToPage(xIndex, yIndex, time);
      } else {
        this.eventList.push(['goToPage', xIndex, yIndex, time]);
      }
      
    },
    refresh: function(){
      if(this.scroll) {
        this.scroll.refresh();
      } else {
        this.eventList.push(['refresh']);
      }
      
    },
    getCurrentPage: function(){
      if(this.scroll){
        return this.scroll.getCurrentPage();
      }
    },
    scrollToElement: function(el, time, offsetX, offsetY, easing){
      if(this.scroll) {
        this.scroll.scrollToElement(el, time, offsetX, offsetY, easing);
      } else {
        this.eventList.push(['scrollToElement', el, time, offsetX, offsetY, easing]);
      }
      
    },
    scrollTo: function(x, y, time) {
      if(this.scroll) {
        this.scroll.scrollTo(x, y, time);
      } else {
        this.eventList.push(['scrollTo', x, y, time]);
      }
    }
  },
  watch: {
  	data: function(){
      this.$nextTick(()=>{
        if(this.scroll) {
          this.scroll.refresh();
        } else {
          this.initScroll();
        }
      });
  	},
    scroll: function() {
      this.eventList.forEach((event)=> {
        let [e, ...params] = event;
        this[e](...params);
      })
    }
  }
};
</script>

<style lang="css" scoped>
</style>


