<template>
  <div class="search" @touchend="blur">
    <div class="search-box">
      <input type="text" class="search-input" placeholder="搜索歌曲,歌手" 
      v-model.trim="searchText" ref="input">
      <div 
      v-show="searchText"
      @click="searchText=''"
      class="close-icon icon">&#xe622;</div>
    </div>
    <scroll 
    @pullingUp="pullUpLoad"
    class="scroll needsclick" 
    :data="searchResults.song.list" :click="true"
    :pullUpLoad="true"
    v-show="searchText && currentPage === 3">
      <search-result :searchResults="searchResults" :loadend="loadend"></search-result>
    </scroll>
    <div class="hotsearch-group">
      <div class="title">热门搜索</div>
      <div 
      class="searchname special" 
      v-if="hotSearchData.special_key"
      @touchend="searchText=hotSearchData.special_key.trim()">{{hotSearchData.special_key}}</div><!-- 
   --><div class="searchname"
      v-if="index < 9"
      v-for="(text,index) of hotSearchData.hotkey"
      @touchend="searchText=text.k.trim()">{{text.k}}</div>
    </div>
  </div>
</template>

<script>
import scroll from '../base/scroll.vue'
import searchResult from './search-result.vue'
    

import jsonp from 'jsonp';
import axios from 'axios';
const HOTURL = 'https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg?g_tk=5381&uin=0&format=json&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&_=1502855869973';
const SEARCHPAGE = 3;
const QQNUMPERPAGE = 10;
const WANGYILIMIT = 10;

export default {

  name: 'search',
  props: {
    currentPage: {
      type: Number,
      default: 0
    },
    slider: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      hotSearchData: {
        hotkey: null,
        special_key: null
      },
      searchResults: {
        song: {
          list: null
        },
        zhida: {
          type: null
        },
        user: {
          img: '',
          name: '',
          signature: ''
        }
      },
      page: 1,
      totalQQMusic: 9999,
      totalWangYi:9999,
      loadend: false,
      searchText: null
    };
  },
  methods: {
    blur() {
      this.$refs.input.blur();
    },
    search(word) {
      const SEARCHURL = `https://c.y.qq.com/soso/fcgi-bin/search_for_qq_cp?g_tk=5381&uin=0&format=jsonp&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&w=${encodeURI(word)}&zhidaqu=1&catZhida=1&t=0&flag=1&ie=utf-8&sem=1&aggr=0&perpage=${QQNUMPERPAGE}&n=20&p=${this.page}&remoteplace=txt.mqq.all&_=1502862347761`;
      const SEARCHSINGERURL = `https://c.y.qq.com/splcloud/fcgi-bin/smartbox_new.fcg?is_xml=0&format=jsonp&key=${encodeURI(word)}&g_tk=5381&jsonpCallback=SmartboxKeysCallbackmod_search169&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0`;

      return new Promise((res, rej)=> {
        if(this.totalQQMusic - this.page*QQNUMPERPAGE > 0) {
          let promise = new Promise((resolve, reject)=> {
            jsonp(SEARCHURL, {param: 'jsonpCallback'}, (err, data)=>{
              if(data.code === 0 && data.data.keyword === this.searchText){
                this.totalQQMusic = data.data.song.totalnum;

                if(this.searchResults.song.list) {
                  for(let song of data.data.song.list){
                    song.platform = 'QQ';
                    this.searchResults.song.list.push(song);
                  }
                } else {
                  this.searchResults = data.data;
                  this.searchResults.song.list.map((song)=> {song.platform = 'QQ';});
                }

                resolve(data.data.zhida);
              }
            });
          });  

          if(!this.searchResults.singer) {
            promise.then((firstSinger)=> {
              jsonp(SEARCHSINGERURL, {param: 'jsonpCallback'}, (err, data)=>{
                let singers = [];

                if(firstSinger.type !== 0) {
                  singers[0] = {
                    mid: firstSinger.singermid,
                    img: `https://y.gtimg.cn/music/photo_new/T001R150x150M000${firstSinger.singermid}.jpg?max_age=2592000`,
                    name: firstSinger.singername,
                    songnum: firstSinger.songnum,
                    albumnum: firstSinger.albumnum
                  };

                  if(data.data.singer) {
                    for(let singer of data.data.singer.itemlist) {
                      if(singer.mid !== singers[0].mid){
                        singer.img = `https://y.gtimg.cn/music/photo_new/T001R150x150M000${singer.mid}.jpg?max_age=2592000`;
                        singers.push(singer);
                      }
                    }
                  }
                }

                if(!this.searchResults.singer){
                  this.$set(this.searchResults, 'singer', []);
                }

                // QQ音乐搜索完毕  继续其他搜索
                res(singers);
              });
            }).catch((err)=> { console.log(err) });
          } else {
            res();
          }
        }
      });
      

    },
    searchWangYi(word, singerList = []) {
      const URL   = `http://localhost:8080/search?keywords=${word}&limit=${WANGYILIMIT}&offset=${(this.page-1)*WANGYILIMIT}`;

      /* 搜索单曲 */
      if(this.totalWangYi - this.page*WANGYILIMIT > 0) {
        axios.get(`${URL}&type=1`).then((data)=> {
          // console.log(data.data)
          if(data.config.url.match(this.searchText)) {
            let list = [];

            for(let music of data.data.result.songs) {
              let flag = true;

              for(let song of this.searchResults.song.list) {
                // 如歌曲不重复，则加入列表
                if(song.songname === music.name &&
                   song.singer[0].name === music.artists[0].name) {
                   flag = false;
                }
              }

              if(flag) {
                music.songname = music.name;
                music.songid = music.id;
                music.singer = music.artists;
                music.platform = 'WangYi';
                list.push(music);
              }
            }

            this.totalWangYi = data.data.result.songCount;
            this.searchResults.song.list.push(...list);
          }
        });
      }

      /* 搜索歌手 */
      axios.get(`${URL}&type=100`).then((data)=> {
        if(data.config.url.match(this.searchText)) {
          for(let artist of data.data.result.artists) {
            let flag = true;  // 判断歌手是否已存在， true: 不存在

            axios.get(`http://localhost:8080/artists?id=${artist.id}`)
            .then((singerData)=> {
              let singer = {
                id: artist.id,
                name: artist.name,
                songnum: singerData.data.artist.musicSize,
                albumnum: artist.albumSize,
                img: artist.picUrl,
                platform: 'WangYi'
              }
              let index, existSinger;

              for([index, existSinger] of singerList.entries()) {
                if(existSinger.name === singer.name) {
                  flag = false;
                  break;
                }
              }

              if(flag) {
                singerList.push(singer);
              } else if(existSinger.songnum < singer.songnum){
                singerList[index] = singer;
              }

              this.$set(this.searchResults, 'singer', singerList)
            });
          }
        }
      });

      /* 搜索用户 */
      axios.get(`${URL}&type=1002`).then((data)=> {
        // console.log(data);
        if(data.config.url.match(this.searchText)) {
          let user = {
            id: data.data.result.userprofiles[0].userId,
            img: data.data.result.userprofiles[0].avatarUrl,
            name: data.data.result.userprofiles[0].nickname,
            signature: data.data.result.userprofiles[0].signature
          };

          this.$set(this.searchResults, 'user', user);
        }
      });
      
    },
    pullUpLoad() {
      if(this.totalWangYi - (this.page)*WANGYILIMIT <= 0 &&
         this.totalQQMusic - (this.page+1)*QQNUMPERPAGE <= 0) {
        this.loadend = true;
      }

      if(!this.loadend) {
        this.page++;
        this.search(this.searchText).then((singerList)=> {
          this.searchWangYi(this.searchText, singerList);
        });  
      }
    }
  },
  watch: {
    currentPage(newVal) {
      if(newVal === SEARCHPAGE) {
        /* init data */
        if(this.hotSearchData.hotkey === null) {
          jsonp(HOTURL, {param: 'jsonpCallback'}, (err, data)=> {
            if(data.code === 0) {
              this.hotSearchData = data.data;
            }
          });
        }

        /* disable slider */
        if(this.searchText) {
          this.slider.disable();
        }
        
      } else {
        this.slider.enable();
      }
    },
    searchText(newVal) {
      this.page = 1;
      this.loadend = false;
      this.searchResults = {
        song: {
          list: null
        },
        zhida: {
          type: null
        },
        user: {
          img: '',
          name: '',
          signature: ''
        }
      };

      if(newVal) {
        clearTimeout(this.timer);
        this.timer = setTimeout(()=> {
          this.search(newVal).then((singerList)=> {
            this.searchWangYi(newVal, singerList);
          });
        }, 500);
        this.slider.disable();
      } else {
        this.totalQQMusic = 9999;
        this.totalWangYi = 9999;
        this.slider.enable();
      }
    }
  },
  components: {
    scroll,
    searchResult
  }
};
</script>

<style lang="scss" scoped>
.search {
  position: relative;
  display: inline-block;
  height: 100vh;
  width: 25%;
  vertical-align: top;

  @at-root
  .search-box {
    display: relative;

    @at-root
    .search-input {
      box-sizing: border-box;
      border-radius: .3rem;
      color:rgba(white, .5);
      font-size: .8rem;
      height: 40px;
      width: 95%;
      background: #222;
      padding:0 1rem;
      margin:1rem 2.5%;
      outline: 0;
      border:0;

      &:focus {
        background: #333;
      }
    }

    @at-root
    .close-icon {
      color: white;
      font-size: .8em;
      position:absolute;
      top: 1.8em;
      right: 1rem;
      width: 1.2rem;
      height: 1.2rem;
      background: #444;
      text-align: center;
      line-height: 1.3em;
      border-radius:50%;
    }
  }

  

  @at-root
  .scroll {
    background: #222;
    position: fixed;
    width:25%;
    top:80px;
    bottom: 60px;
    overflow: hidden;
  }
  

  @at-root
  .hotsearch-group {
    width:90%;
    margin:0 5%;
    @at-root
    .title {
      color: rgba(white, .6);
      font-size: .75em;
      margin-bottom: .5rem;
    }

    @at-root
    .searchname {
      color: rgba(white, .4);
      background: #333;
      border-radius: .2rem;
      display: inline-block;
      font-size: .7rem;
      margin: .3rem;
      padding: .3rem.5rem;
    }

    @at-root
    .special {
      color: rgba(#ffcd32, .8);
    }
  }
}
</style>


