import decode from '../api/decode.js';
import axios from 'axios';

let Song =  function(song) {
  this.platform = song.platform;
  this.currentTime = 0;

  if(this.platform === 'QQ') {
    return this.setQQMusic(song);
  } else if(this.platform === 'WangYi') {
    return this.setWangYiMusic(song);
  }
  
  

};

Song.prototype.setQQMusic = function(song) {
  return new Promise((resolve, reject)=> {
    this.songid = song.songmid;
    this.songmid = song.songmid;
    this.albummid = song.albummid;
    this.albumImg_small = `https://y.gtimg.cn/music/photo_new/T002R68x68M000${song.albummid}.jpg?max_age=2592000`;
    this.albumImg_big = `https://y.gtimg.cn/music/photo_new/T002R300x300M000${song.albummid}.jpg?max_age=2592000`;
    this.albumname = decode(song.albumname);
    this.songUrl = `http://ws.stream.qqmusic.qq.com/C100${song.songmid}.m4a?fromtag=0`;
    this.songName = decode(song.songname);
    this.singer = '';
    song.singer.forEach((singer, index)=> {
      if(index > 0){
        this.singer += '&';
      }
      this.singer += decode(singer.name);
    });

    resolve(this);
  });
  
}

Song.prototype.setWangYiMusic = function(song) {
  this.songid = song.id;
  this.songmid = song.id;
  this.albummid = song.album.id;
  this.albumname = decode(song.album.name);
  this.songName = decode(song.name);
  this.singer = '';
  song.artists.forEach((singer, index)=> {
    if(index > 0){
      this.singer += '&';
    }
    this.singer += decode(singer.name);
  });
  if(song.img) {
    this.albumImg_small = `${song.img}?param=68y68`;
    this.albumImg_big = `${song.img}?param=300y300`;
  }

  return Promise.resolve(this);
  /* return Promise.all([
    axios.get(`http://112.74.38.137:8080/album?id=${this.albummid}`),
    axios.get(`http://112.74.38.137:8080/music/url?id=${this.songid}`)
    
  ]).then(([album, song])=> {
    this.albumImg_small = `${album.data.album.picUrl}?param=68y68`;
    this.albumImg_big = `${album.data.album.picUrl}?param=300y300`;

    this.songUrl = song.data.data[0].url;

    return this;
  }); */
}

Song.audition = function(song) {
  let music = {
    songid: song.songmid,
    songmid: song.songmid,
    albummid: song.albummid,
    songName: song.songname,
    singer: song.singer,
    platform: song.platform
  };

  if(music.platform === 'QQ') {
    music.albumImg_small = `https://y.gtimg.cn/music/photo_new/T002R68x68M000${song.albummid}.jpg?max_age=2592000`;
    music.albumImg_big = `https://y.gtimg.cn/music/photo_new/T002R300x300M000${song.albummid}.jpg?max_age=2592000`;
    music.songUrl = `http://ws.stream.qqmusic.qq.com/C100${song.songmid}.m4a?fromtag=0`;
    return Promise.resolve(music);
  } else if(music.platform === 'WangYi') {
    return Promise.resolve(music);
    // return Promise.all([
    //   axios.get(`http://112.74.38.137:8080/album?id=${music.albummid}`),
    //   axios.get(`http://112.74.38.137:8080/music/url?id=${music.songid}`)
      
    // ]).then(([album, song])=> {
    //   music.albumImg_small = `${album.data.album.picUrl}?param=68y68`;
    //   music.albumImg_big = `${album.data.album.picUrl}?param=300y300`;

    //   music.songUrl = song.data.data[0].url;

    //   return music;
    // });
  }
}

export {Song};


